.link,a {
	display: inline;
	color: inherit;
	font-size: inherit;
	line-height: inherit;
	text-decoration: none;
	transition: 0.4s ease;
	outline: none;
	font-weight: 400;

	&:hover {
		color: $blue; }

	&--default {
		color: $baseTxt;
		&:hover {
			color: $orange; } }

	&--bor {
		position: relative;
		&:after {
			content: '';
			width: 100%;
			height: 1px;
			position: absolute;
			left: 0;
			bottom: -2px;
			background-color: $baseTxt;
			opacity: 0.3;
			transform: scale(1);
			transform-origin: 100% 100%;
			transition: $transition; }
		&:hover:after {
			transform: scale(0); } }

	&--bor-white {
		color: $white;
		&:hover {
			color: $white; }
		&:after {
			background-color: $white; } }

	&--white-mark {
		font-weight: 500;
		color: $white;
		letter-spacing: 0.42px;
		font-size: rem(21px);
		&:hover {
			color: $white; } }

	&--white {
		&:after {
			transform: scale(0); }
		&:hover {
			&:after {
				transform: scale(1); } } }

	&--arrow {
		display: inline-flex;
		align-items: center;

		svg {
			margin: 0 5px 0 0;
			width: 24px;
			height: 24px;
			fill: transparent;
			transition: 0.35s ease;
			stroke-width: 1.2px;
			stroke: $baseTxt; }

		&:hover {
			color: $orange;
			svg {
				stroke: $orange; } } }


	@include bp($bp-large) {
		&--white-mark {
			font-size: rem(18px); } }

	@include bp($bp-medium) {
		&--arrow {
			svg {
				width: 18px;
				height: 18px; } }

		&--white-mark {
			font-size: rem(13px); } }

	@include bp($bp-small) {
		&--white-mark {
			font-size: rem(8px); } } }

